const selects = document.querySelectorAll(".select");

if (selects) {
  selects.forEach((select) => {
    const selectTrigger = select.querySelector(".select__trigger");
    const selectBtn = select.querySelector(".select__btn");
    const selectList = select.querySelector(".select__list");
    const selectInput = select.querySelector(".select__input");
    const selectOptions = selectList.querySelectorAll(".select__option");

    selectBtn.addEventListener("click", () => {
      selectList.classList.toggle("is-active");
      selectTrigger.classList.toggle("is-active");
    });

    selectOptions.forEach((item) => {
      item.addEventListener("click", (event) => {
        event.stopPropagation();

        if (item.classList.contains("is-active")) {
          item.classList.remove("is-active");
        }

        item.classList.add("is-active");
        selectBtn.innerText = item.innerText;
        selectInput.value = item.dataset.value;
        selectList.classList.remove("is-active");
        selectTrigger.classList.remove("is-active");
      });
    });

    document.addEventListener("click", (e) => {
      if (e.target !== selectBtn) {
        selectList.classList.remove("is-active");
        selectTrigger.classList.remove("is-active");
      }
    });

    document.addEventListener("keydown", (e) => {
      if (e.key === "Tab" || e.key === "Escape") {
        selectList.classList.remove("is-active");
        selectTrigger.classList.remove("is-active");
      }
    });
  });
}
